import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { Navbar, Sidebar, Footer } from './components'

import { Home, SingleProduct, Cart, Checkout, Error, About, Products, PrivateRoute, Auth } from "./pages";

// import About from './pages/AboutPage';

function App() {
  return <Router>
    <Navbar />
    <Sidebar />
      <Switch>
        <Route exact path="https://ravikesh4.github.io/shopping-react/">
          <Home />
        </Route>
        <Route exact path="https://ravikesh4.github.io/shopping-react/cart">
          <Cart />
        </Route>
        <Route exact path="https://ravikesh4.github.io/shopping-react/checkout">
          <Checkout />
        </Route>
        <Route exact path="https://ravikesh4.github.io/shopping-react/about">
          <About />
        </Route>
        <Route exact path="https://ravikesh4.github.io/shopping-react/products">
          <Products />
        </Route>
        <Route exact path="https://ravikesh4.github.io/shopping-react/products/:id" children={<SingleProduct />} />
        <Route path="*">
          <Error />
        </Route>
                
        
      </Switch>
      <Footer />
    </Router>
}

export default App
